import React from 'react'

import ReactQuill from 'react-quill'

class Comment extends React.Component {
	handlePress(e) {
		e.stopPropagation()
	}
	render() {
		let { body_html, onChange } = this.props
		return (
			<div className="Comment">
				<ReactQuill onKeyDown={this.handlePress} theme={null} value={body_html} onChange={onChange} />
			</div>
		)
	}
}

export default Comment;