import React from 'react'

class Thread extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			thread: ''
		}
	}
	onSubmit(e) {
		e.preventDefault()
		this.props.onSelection(this.state.thread)
	}
	handle(e) {
		this.setState({ thread: e.target.value })
	}
	render() {
		return (
			<div className="Thread">
				<h1>Choose a thread</h1>
				<form onSubmit={this.onSubmit.bind(this)}>
					<input value={this.state.thread} onChange={this.handle.bind(this)} placeholder="Choose a thread" autoFocus />
					<button>OK</button>
				</form>
			</div>
		)
	}
}

export default Thread;